:host {
  display: block;
  --ir-dialog-max-width: 32rem;
}
.logo {
  height: 20px;
  width: 10px !important;
}

.button-link {
  padding: 8px 14px;
  font-size: 14px;
  line-height: 14px;
  background: white;
  color: var(--gray-600, #475467);
}
.button-link:hover {
  text-decoration: underline;
}
.button-link:disabled {
  background: white;
  color: var(--gray-300, #d0d5dd);
}

.button-outline {
  all: unset;
  border-radius: var(--radius, 8px);
  color: white;
  font-weight: 500;
  cursor: pointer;
  gap: 8px;
  transition: all 0.3s ease-in-out;
  width: var(--ir-btn-width, inherit);
  box-sizing: border-box;
  background: white;
  color: var(--gray-700, #344054);
  display: flex;
  align-items: center;
  padding: 8px 14px;
  font-size: 14px;
  line-height: 14px;
}
.button-outline .loader {
  background-color: var(--gray-700, #344054);
}
.button-outline:hover {
  background: var(--gray-100, #fcfcfd);
  color: var(--gray-700, #344054);
}
.button-outline:disabled {
  background: var(--gray-200, #eaecf0);
  color: var(--gray-300, #d0d5dd);
}
